import React from "react"
import DatePicker, { Calendar } from "../../../build/index"

export default function (translate, language) {
  let calendars = {
    title: translate("Calendars"),
    description: translate("calendars"),
    jsx: <table>
      <thead>
        <tr>
          <th>{translate("Calendar")}</th>
          <th>{translate("Value")}</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{translate("gregorian")}</td>
          <td>gregorian</td>
        </tr>
        <tr>
          <td>{translate("persian")}</td>
          <td>persian</td>
        </tr>
        <tr>
          <td>{translate("arabic")}</td>
          <td>arabic</td>
        </tr>
        <tr>
          <td>{translate("indian")}</td>
          <td>indian</td>
        </tr>
      </tbody>
    </table>
  }

  let locales = {
    title: translate("Locales"),
    description: translate("locales"),
    jsx: <table>
      <thead>
        <tr>
          <th>{translate("Language")}</th>
          <th>{translate("Value")}</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{translate("english")}</td>
          <td>en</td>
        </tr>
        <tr>
          <td>{translate("fa")}</td>
          <td>fa</td>
        </tr>
        <tr>
          <td>{translate("ar")}</td>
          <td>ar</td>
        </tr>
        <tr>
          <td>{translate("hi")}</td>
          <td>hi</td>
        </tr>
      </tbody>
    </table>
  }

  const examples = {
    title: "Descriptions",
    description: "calendar_descriptions"
  }

  const gregorian = {
    title: "default_calendar",
    code: `<Calendar /> `,
    jsx: <Calendar />
  }

  const defaultDatepicker = {
    title: "Default DatePicker",
    code: `<DatePicker /> `,
    jsx: <DatePicker />
  }

  const persian = {
    title: "calendar_example_persian",
    code: `<Calendar
  calendar="persian"
  locale="fa"
/> `,
    jsx: <Calendar
      calendar="persian"
      locale="fa"
    />
  }

  const persianDatepicker = {
    title: "Persian DatePicker",
    description: "persian_datepicker_description",
    code: `<div style={{ direction: "rtl" }}>
  <DatePicker
    calendar="persian"
    locale="fa"
    calendarPosition="bottom-right"
  />
</div>`,
    jsx: <div style={{ direction: "rtl" }}>
      <DatePicker
        calendar="persian"
        locale="fa"
        calendarPosition="bottom-right"
      />
    </div>
  }

  const arabic = {
    title: "calendar_example_arabic",
    code: `<Calendar
  calendar="arabic"
  locale="ar"
/> `,
    jsx: <Calendar
      calendar="arabic"
      locale="ar"
    />
  }

  const indian = {
    title: "calendar_example_indian",
    code: `<Calendar
  calendar="indian"
  locale="hi"
/> `,
    jsx: <Calendar
      calendar="indian"
      locale="hi"
    />
  }

  const datepicker = {
    title: "calendar_example_datepicker",
    code: `<DatePicker
  calendar="persian"
  locale="en"
${language === "en" ? "/> " : `  calendarPosition="auto-right"
/> `}`,
    jsx: <DatePicker
      calendar="persian"
      locale="en"
      calendarPosition={language === "fa" ? "bottom-right" : "bottom-left"}
    />
  }

  return [
    calendars,
    locales,
    examples,
    gregorian,
    defaultDatepicker,
    persian,
    persianDatepicker,
    arabic,
    indian,
    datepicker
  ]
}